import React from 'react';
import bridge from '@vkontakte/vk-bridge';
import { Spinner, Avatar, ModalRoot, ModalPage, ModalPageHeader, Tabs, TabsItem, List, Cell, Card, CardGrid, Checkbox, Radio, Input, Button, FormLayout, Header} from '@vkontakte/vkui';
import Axios from 'axios';
import '@vkontakte/vkui/dist/vkui.css';
import qr from '@vkontakte/vk-qr';

import { VKMiniAppAPI } from '@vkontakte/vk-mini-apps-api';

import Icon56FaceIdOutline from '@vkontakte/icons/dist/56/face_id_outline';
import Icon56WriteOutline from '@vkontakte/icons/dist/56/write_outline';
import Icon16Fire from '@vkontakte/icons/dist/16/fire';

const api = new VKMiniAppAPI();
const url = 'https://bot-api-vk.000webhostapp.com/api.place_quests/';


class Modal extends React.Component {
    constructor(props) {
        super(props)
          this.state = {
              activeModal: null,
              activeTab:'all',
              actievedChose:false,
              hash:"",
              rating_team:null,
              rating_all:null,
              qr:null,
              count:0
              
          }
    }

      componentDidMount() {
        api.initApp();
        api.onLocationChanged(this.set)
      }


      set = (o) => {
        switch(o.location){

          case '':
            this.setState({activeModal:null})
          break;
          case 'qr':
              const isShowVkLogo = true;
              const e = qr.createQR(this.props.user.id, {isShowVkLogo});
                this.setState({activeModal:'qr'});
                document.getElementById('img_qr').innerHTML=e;  

          break;

          case 'rating':
           this.setState({activeModal:'rating'});
           Axios.post(url, JSON.stringify({type:"get_rating"})).then(data=>{
            console.log(data);
            this.setState({rating_all: data.data})
            })
          Axios.post(url, JSON.stringify({type:"get_rating_team"}))
          .then(data=>{
            this.setState({rating_team: data.data})
          })  
          break;

          case 'join':
            this.setState({activeModal:'join'});

          break;
          case 'transfer':
            this.setState({activeModal:'transfer'});
           break; 
        }
      }



      onChange =(e)=> {
        const { name, value } = e.currentTarget;
        this.setState({ [name]: value });
      }


      pay =()=>{
        api.setLocationHash("");
         api.openCodeReader()
         .then(data=>{
           alert(data);
           
         })
      }



      render() {
          return(
              <ModalRoot activeModal={this.state.activeModal}>
                <ModalPage id="rating" onClose={() =>api.setLocationHash("")} header={<ModalPageHeader>Рейтинг</ModalPageHeader>} dynamicContentHeight>

                    <Tabs>
                      <TabsItem selected={this.state.activeTab == 'all'} onClick={()=>this.setState({activeTab: 'all'})}>Личный</TabsItem>
                      <TabsItem selected={this.state.activeTab == 'friends'} onClick={()=>this.setState({activeTab: 'friends'})}>Командный</TabsItem>
                    </Tabs>


                    {this.state.activeTab === 'all' ? 
                     <div>
                       {this.state.rating_all != null ? 
                     <List>
                       {this.state.rating_all.map((data,i)=>
                         <Cell key={i} before={<Avatar src={data.photo_200} />} indicator={"+"+data.k+" /c"} asideContent={data.current_ballans}>{data.name}</Cell>
                       )}
                        
                     </List>:
                     <Spinner size="large" style={{marginTop:5}}/> }
                     </div>  
                     :
                     <List>
                         {this.state.rating_team.map((data,i)=>
                         <Cell key={i} before={<Avatar src={data.img} />} multiline  asideContent={data.sum} indicator={"+"+data.k+" /c"}>{data.name}</Cell>
                       )}
                     </List>
      }
                </ModalPage>

                <ModalPage id="join" header={<ModalPageHeader>Присоединиться</ModalPageHeader>} onClose={() =>api.setLocationHash("")} dynamicContentHeight>
                  <CardGrid style={{marginTop:0, paddindBottom:100}}>
                    <Card mode="shadow" size="m" >
                      <div style={{height:90}} align="center" onClick={()=>{this.props.joinQR(); this.setState({actievedChose:false}) ;this.props.back()}}>
                         <Icon56FaceIdOutline style={{marginTop:12}}/>
                         <span>Отсканировать QR</span>
                      </div>
                    </Card>
                   
                    <Card mode="shadow" size="m" >
                      <div style={{height:90}} align="center" onClick={()=>this.setState({actievedChose:true})}>
                        {this.state.actievedChose ? <Icon16Fire style={{position:"absolute", right:"7%"}} fill="#FF681C"/>:<div />}
                       
                        <Icon56WriteOutline style={{marginTop:12}}/>
                        <span>По хэшу</span>
                      </div>
                    </Card>
                  </CardGrid>
                 {this.state.actievedChose ? <FormLayout>
                   <Input placeholder="Введите хэш" style={{marginTop:12, marginBottom:1}} name="hash" onChange={this.onChange}/>
                 <Button size="xl" onClick={()=>{this.props.join_by_hash(this.state.hash); this.props.back()}}>OK</Button></FormLayout>:<div />}
                </ModalPage>

                <ModalPage id="qr" onClose={() =>api.setLocationHash("")} header={<ModalPageHeader>Принять по QR</ModalPageHeader>} dynamicContentHeight>
                  <div align="center">
                 <object id="img_qr"></object>
                 </div>
                </ModalPage>

                <ModalPage id="transfer" onClose={() =>api.setLocationHash("")} header={<ModalPageHeader>Введите сумму перевода</ModalPageHeader>}>
                 <FormLayout>
                   <Input name="count" onChange={this.onChange}/>
                   <Button size="xl" onClick={this.pay}>Продолжить</Button>
                 </FormLayout>
                </ModalPage>
              </ModalRoot>
          )
      }
}

export default Modal