import React from 'react';
import bridge from '@vkontakte/vk-bridge';
import {View, Panel, Group, PanelHeader, Spinner, Header, Card, CardGrid, Avatar, Snackbar, ConfigProvider, PanelHeaderButton} from '@vkontakte/vkui';
import Axios from 'axios';

import '@vkontakte/vkui/dist/vkui.css';

import Modal from './panels/Modal';
import Icon48Camera from '@vkontakte/icons/dist/48/camera';
import Icon56LinkCircleOutline from '@vkontakte/icons/dist/56/link_circle_outline';
import { VKMiniAppAPI } from '@vkontakte/vk-mini-apps-api';
import Icon28CancelCircleOutline from '@vkontakte/icons/dist/28/cancel_circle_outline';
import Icon28DoneOutline from '@vkontakte/icons/dist/28/done_outline';
import Icon28Play from '@vkontakte/icons/dist/28/play';
import Icon28Pause from '@vkontakte/icons/dist/28/pause';
import Icon56UserAddOutline from '@vkontakte/icons/dist/56/user_add_outline';
import Icon56DeleteOutlineAndroid from '@vkontakte/icons/dist/56/delete_outline_android';
import Icon56TouchIdOutline from '@vkontakte/icons/dist/56/touch_id_outline';
import Icon56MoneyTransferOutline from '@vkontakte/icons/dist/56/money_transfer_outline';

import vkQr from '@vkontakte/vk-qr';
import coin from './tree_coin.png';



const api = new VKMiniAppAPI();
const url = 'https://bot-api-vk.000webhostapp.com/api.place_quests/';

class App extends React.Component {
 constructor(props) {
   super(props)
   this.state = {
	  user:null,
	  userInfo:null,
	  activeModal:null,
	  snackbar:null,
	  scheme: "bright_light",
	  o:null,
	  start:false,
	  intId:null,
	  isHaveGroup:false
   }
   this.updateConfig = this.updateConfig.bind(this);
 }

    componentDidMount() {
		api.initApp();
		
		
		api.onUpdateConfig(this.updateConfig)
		Axios.post(url,JSON.stringify({type:"log_in",obj:{url:window.location.href}})).then(data=>{
			console.log(data)
			 this.setState({user:data.data});
            Axios.post(url, JSON.stringify({type:"if_have_group", hash:data.data.hash})).then(data=>{
				console.log(data);
				if(data.data == 'ok') {
					this.setState({isHaveGroup:true})
				}
			})
		   })
       
	}

	start_and_pause = () => {
		console.log(this.state.start);
		
		if(this.state.start == false){
		  let intervalId = setInterval(()=>{
			Axios.post(url, JSON.stringify({type:'tick', hash:this.state.user.hash}))
			.then(data=>{this.setState({user:{
				...this.state.user,
				current_ballans:(data.data).toFixed(3)
			}})})
		  }, 999)
		   this.setState({intId:intervalId, start:!this.state.start});
		} else {
			clearInterval(this.state.intId);
			this.setState({start:!this.state.start});
		}
	}

	 updateConfig (o) {
		console.log(o);
		this.setState({scheme:o.scheme})
	 }


	setSnackbar = (data) => {
		this.setState({snackbar: data});
	}	

	openQRreader = () => {
		api.openCodeReader()
		.then(data=>{
			Axios.post(url, JSON.stringify({type:"set_k", hash:this.state.user.hash, key:data}))
			.then(data=>{
               if(data.data == 'not') {
				   this.setSnackbar(<Snackbar before={<Icon28CancelCircleOutline fill="#FB2626"/>} onClose={()=>this.setState({snackbar: null})}>Ошибочка</Snackbar>)
			   }else {
				   this.setSnackbar(<Snackbar before={<Icon28DoneOutline fill="#00CE40"/>} onClose={()=>this.setState({snackbar: null})}>Ok</Snackbar>)
				this.setState({user:{
					...this.state.user,
					k:(Number(this.state.user.k)+Number(data.data)).toFixed(3)
				}})
			   }
			})
		})
	}	

	join_by_hash = (hash) => {
		Axios.post(url, JSON.stringify({type:"join_group", hash:this.state.user.hash, key:hash}))
		.then(data=>{
			if(data.data == 'not') {
			  this.setSnackbar(<Snackbar before={<Icon28CancelCircleOutline fill="#FB2626"/>} onClose={()=>this.setState({snackbar: null})}>Ошибочка</Snackbar>)
			}else {
			  this.setSnackbar(<Snackbar before={<Icon28DoneOutline fill="#00CE40"/>} onClose={()=>this.setState({snackbar: null})}>Ok</Snackbar>)
			}
		})
	}

	generateQR = () => {
		api.getUserInfo().then(data=>{
			const isShowVkLogo = true;
			const qrSvg = vkQr.createQR(this.state.user.id, isShowVkLogo);
			api.setLocationHash("qr")
			this.setState({qr:qrSvg})
		})

	}


	  joinQR = () => {
		  api.openCodeReader()
		  .then(data=>{
			  Axios.post(url, JSON.stringify({type:"join_group", hash:this.state.user.hash, key:data}))
			  .then(data=>{
				  if(data.data == 'not') {
					this.setSnackbar(<Snackbar before={<Icon28CancelCircleOutline fill="#FB2626"/>} onClose={()=>this.setState({snackbar: null})}>Ошибочка</Snackbar>)
				  }else {
					this.setSnackbar(<Snackbar before={<Icon28DoneOutline fill="#00CE40"/>} onClose={()=>this.setState({snackbar: null})}>Ok</Snackbar>)
				  }
			  })
		  })
	  }



	

	render() {
		return(
	<ConfigProvider isWebView={true} scheme={this.state.scheme}>		
		<View activePanel="home" modal={<Modal activeModal={this.state.activeModal} 
											   back={()=>this.setState({activeModal:null})} 
											   joinQR={this.joinQR}
											   user={this.state.user}
											   join_by_hash={this.join_by_hash}
											   />}>
          <Panel id="home">
           <PanelHeader left={<PanelHeaderButton onClick={()=>{this.start_and_pause()}}>{this.state.start ? <Icon28Pause  /> : <Icon28Play />}</PanelHeaderButton>}>Home</PanelHeader>
            {this.state.user == null ? <Spinner size="large"/> :
			  <Group align="center" header={<Header mode='secondary' style={{textAlign: 'center'}} aside={"+"+this.state.user.k}>Ваш счет</Header>}>
				  <div>
			     <span id="balls" style={{fontSize:32, fontWeight:"bold"}}>{this.state.user.current_ballans} <Avatar src={coin} size={42} style={{position:"absolute", right:'5%', marginTop:-42}}/></span>
				  </div>
				 <CardGrid style={{paddingTop:20, paddingBottom:15}}>
				   <Card size='l' style={{backgroundImage: 'linear-gradient(135deg, #f24973 0%, #3948e6 100%)'}} onClick={this.openQRreader}>
					   <div style={{height:85}} align='center'>
						   <Icon48Camera fill="#FFFFFF" style={{marginTop:12}}/>  
						   <span color='white' style={{color: 'white'}}>Сканировать</span>
					   </div>
				   </Card>
				   <Card size='l'>
				        <div style={{height:85}} align='center' onClick={()=>{api.setLocationHash("rating")}}>
							<Icon56LinkCircleOutline style={{marginTop:12}}/>
							<span>Рейтинг</span>
						</div>
				   </Card>
				   {this.state.isHaveGroup == false ? 
				   	 <Card size='l' style={{backgroundImage: 'linear-gradient(135deg, #9AFF7F 0%, #0BE1F5 100%)'}}>
						  <div style={{height:85}} align='center' onClick={()=>{api.setLocationHash("join")}}>
						  <Icon56UserAddOutline style={{marginTop:12}} />
						  <span>Присоединиться к группе</span>
					   </div>
					 </Card>:
					 <Card size='l' style={{backgroundColor:"#FA5959"}}>
						 <div style={{height:85}} align='center' onClick={()=>{Axios.post(url, JSON.stringify({type:'delete',hash:this.state.user.hash}));this.setState({isHaveGroup:false})}}>
                           <Icon56DeleteOutlineAndroid style={{marginTop:12}}/>
						   <span>Выйти из группы</span>
						 </div>
					 </Card>
				}
                    <Card size='m' style={{ backgroundColor:"#7C4BFA"}}>
                      <div style={{height:85}} align='center' onClick={()=>{this.generateQR(); this.setState({activeModal:'qr'})}}>
						  <Icon56TouchIdOutline fill="#fff" style={{marginTop:12}}/>
						  <span style={{color:"white"}}>Принять</span>
					  </div>
					</Card>

					<Card size='m' style={{ backgroundColor:"#448BF3"}}>
						<div style={{height:85}} align='center' onClick={()=>api.setLocationHash("transfer")}>
							<Icon56MoneyTransferOutline fill="#fff" style={{marginTop:12}}/>
							<span style={{color:"white"}}>Перевести</span>
						</div>
					</Card>
				 </CardGrid>
			  </Group>
			}
			{this.state.snackbar}
		  </Panel>
		</View>
	</ConfigProvider>	
		)
	}

}
export default App;

